.explore {
  background-color: $bl;
  @extend %jeko-700;

  &__inner {
    display: flex;
    padding: 300px 0 250px;
  }
  &__direct {
    font-size: 18px;
    line-height: 32px;
    color: $black-gray;
    margin-right: 65px;
  }
  &__title {
    font-size: 56px;
    line-height: 64px;
    margin-bottom: 60px;
    text-underline-offset: 3px;
    text-decoration: underline 3px;
  }
  &__block {
    max-width: 490px;
  }
  &__link {
    font-size: 20px;
    padding-right: 105px;
    position: relative;
    &::after {
      position: absolute;
      content: "";
      top: 50%;
      right: 0;
      background-image: url("../images/gold-gate/arrow.svg");
      background-repeat: no-repeat;
      width: 70px;
      height: 16px;
      transform: translateY(-50%);
    }
  }
}